.file_upload_button {
  text-align: center;
  background-color: #FFF;
  border-radius: 4px;
  padding: 10px;
  width: 120px;
  margin-top: 20px;
  cursor: pointer;
}
.vc_row .file_upload_button {
  background-color: #EEE;
}
.file_upload_button .dashicons {
  font-size: 100px;
  height: inherit;
  width: inherit;
  cursor: pointer;
}
.file_upload_button div {
  cursor: pointer;
  font-weight: bold;
}
.file_upload_button .loading {
  visibility: hidden;
  cursor: pointer;
  width: 100px;
  height: 10px;
  margin: 0 auto 10px;
}
.file_upload_button .loading .progress {
  overflow: hidden;
}
.file_upload_button .loading .progress img {
  width: 100px;
  height: 10px;
}

#snc-media_upload_file_wrap .title {
  background: transparent url("../../images/icon-upload_file.png") no-repeat 0 0;
  padding-left: 30px;
}
#snc-media_upload_file_wrap .progress {
  display: none;
  position: fixed;
  bottom: 40px;
}
#snc-media_upload_file_wrap .progress #snc-progress_bar_wrapper {
  border: 1px solid #CCCCCC;
  border-radius: 1px;
}
#snc-media_upload_file_wrap .progress #snc-progress_bar_wrapper #snc-progress_bar {
  background-color: #3399cc;
  color: white;
  font-size: 23px;
  padding: 15px 10px;
}

.snc_TB {
  padding: 30px;
}
.snc_TB > span.dashicons {
  font-size: 22px;
}
.snc_TB .title {
  font-size: 23px;
  display: inline-block;
  padding: 0;
}
.snc_TB #snc-media_upload_message {
  display: none;
}
.snc_TB #snc-media_upload_file {
  display: none;
}
.vc_row .snc_TB {
  margin: 0px;
  border-top: 1px solid #DDDDDD;
}

.file-selector ul {
  display: none;
  margin-left: 20px;
}
.file-selector .structure-dir-btn {
  display: flex;
  cursor: pointer;
  background-color: transparent;
  transition: background-color 0.2s ease-in;
}
.file-selector .structure-dir-btn:hover {
  background-color: #DDDDDD;
}
.file-selector .structure-dir-btn:before {
  font: 400 20px/1 dashicons;
  content: "\f139";
}
.file-selector .structure-dir-btn.open:before {
  font: 400 20px/1 dashicons;
  content: "\f140";
}
.file-selector .file-selector {
  cursor: pointer;
  background-color: transparent;
  transition: background-color 0.2s ease-in;
}
.file-selector .file-selector:hover {
  background-color: #DDDDDD;
}